@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
html {
  scroll-behavior: smooth;
}
* {
  box-sizing: border-box;
}
body {
  margin: 0;
  margin: 0;
  width: 100vw;
  height: auto;
  min-height: 100vh;
  overflow-x: hidden;
  font-family: 'Montserrat', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
a {
  color: inherit;
  text-decoration: none;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  padding: 60px;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.navbar {
    width: 100vw;
    height: 70px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 0 60px;
    background-color: black;
    color: orange;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    left: 0;
    z-index: 100;
}
.brand {
    font-size: 2rem;
    font-weight: 300;
    letter-spacing: 20px;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    cursor: pointer;
}
.brand:hover {
    color: blue;
}
.search_bar {
    margin-left: auto;
    height: 100%;
    display: flex;
    grid-gap: 10px;
    gap: 10px;
    align-items: center;
}
.search_input{
    border: none;
    border-radius: 20px;
    outline: none;
    height: 35px;
    padding: 0 8px;
    font-style: inherit;
    width: 200px;
    text-align: center;
}
.search_bar button {
    border: none;
    border-radius: 20px;
    outline: none;
    background-color: orange;
    /* color: white; */
    font-style: inherit;
    height: 35px;
    width: 100px;
    padding: 0 20px;
}
.cardcard {
    width: 50%;
    height: 300px;
    display: flex;
    background-color: whitesmoke;
    padding: 20px;
    color: black;
    grid-gap: 20px;
    gap: 20px;
    margin: 20px 0;
    position: relative;
    transition: 0.5s;
    overflow: hidden;
    border-radius: 20px;
}
.cardcard:hover{
    width: 60%;
    height: 400px;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    cursor: pointer;
    color: crimson;
    font-size: 2rem;
}
.card_left {
    width: 30%;
    height: 100%;
}
.avatar {
    width: 100%;
    object-fit: cover;
}
.card_right {
    width: 70%;
    height: 20%;
}
.username {
    text-align: left;
    font-weight: 300;
    font-size: 2.5rem;
    padding: 0;
    margin: 0;
    color: blue;
}
.biodata {
    text-align: left;
    line-height: 1.5;
}
.sidemenu {
  width: 250px;
  height: auto;
  display: flex;
  flex-direction: column;
  background-color: orange;
  position: absolute;
  top: 100px;
  left: 60px;
}

.sm_item {
  width: 100%;
  height: 35px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 0 10px;
  color: white;
}

.sm_item:hover {
  background-color: rgb(214, 141, 4);
}

.sm_item_active span{
    color: orangered;
    background-color: black;
}
